@keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
}
  
.spinner {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

#githubLogo {
    animation: spin 2s linear infinite;
    fill: var(--very-light-gray);
}